<template>
  <div>
    <vs-input
      data-vv-validate-on="blur"
      name="email"
      icon-no-border
      icon="icon icon-user"
      icon-pack="feather"
      label-placeholder="email"
      v-model="email"
      class="w-full"/>
    <span v-if="emailError" class="text-danger text-sm">{{ emailError }}</span>

    <vs-input
      data-vv-validate-on="blur"
      name="password"
      icon-no-border
      icon="icon icon-key"
      icon-pack="feather"
      label-placeholder="password"
      type="password"
      v-model="password"
      class="w-full mt-8"/>
    <span v-if="passwordError" class="text-danger text-sm">{{ passwordError }}</span>

    <div class="flex flex-wrap justify-between mt-12 mb-8">
      <vs-button id="login-btn" :disabled="!validateForm" @click="loginUser">Login</vs-button>
      <vs-button type="border" @click="goToRegisterPage">Register</vs-button>
    </div>
  </div>
</template>

<script>
import jwt from '../../../../http/requests/auth/jwt';
import mixins from "../../../../mixins";
import axios from '../../../../http/axios'

export default {
  mixins: [mixins],
  data() {
    return {
      email: '',
      password: '',

      emailError: null,
      passwordError: null,
    }
  },
  computed: {
    validateForm() {
      return this.email !== '' && this.password !== ''
    }
  },
  mounted() {

  },
  methods: {

    loginUser() {

      let emailVal = this.validateEmail(this.email);
      this.emailError = emailVal.message;

      if (!emailVal.validation) {
        return;
      }


      this.startLoading('login-btn');

      axios.post(`/api/auth/login`, {
        email: this.email,
        password: this.password
      },{
        headers:{
          Accept: "application/json",
        }
      }).then(res => {
        if (res.status === 200){
          console.log(res.data);
          if (!res.data.error){
            this.simpleNotify(this.NOTIFY_SUCCESS_TITLE, res.data.message, 'success');
            this.saveUser(res.data);
            this.$router.push({name: 'dashboard'});
          }else if (res.data.code === 400 || res.data.code === 404){
            this.simpleNotify(this.NOTIFY_DANGER_TITLE, res.data.message, 'danger');
          }
        }else {
          this.simpleNotify(this.NOTIFY_DANGER_TITLE, 'something went wrong', 'danger');
        }
        this.stopLoading('login-btn');
      })

    },

    goToRegisterPage() {
      this.$router.push('/register').catch(() => {
      })
    }
  }
}

</script>

